// -----------Angular/------------
// 
import { BrowserModule } from '@angular/platform-browser';
// 
import { NgModule, APP_INITIALIZER } from '@angular/core';
// 
import { FormsModule } from '@angular/forms';
// 
import { HttpClientModule } from '@angular/common/http';
// Angular
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
// Nfif, Ngfor
import { CommonModule } from '@angular/common';

import { RouterModule } from '@angular/router';
// -------------------
import { NgZorroAntdModule, NZ_I18N, zh_CN } from 'ng-zorro-antd';

// 
import { AppRoutingModule } from './app-routing.module';
// vue/ OR
import { AppComponent } from './app.component';
// ICON
import { IconsProviderModule } from './icons-provider.module';

// ----------layout------------
import { LayoutModule } from './layout/layout.module';
// 
import { PagesModule } from './pages/pages.module';
// 
import { ReactiveFormsModule } from '@angular/forms';

// angular i18n
import { registerLocaleData } from '@angular/common';
import zh from '@angular/common/locales/zh';

// 
import { StartupServiceFactory } from './core/startup/startup.factory';
import { StartupService } from './core/startup/startup.service';

// 

import { CommonCoModule } from './common/common.component';
 

import '../app/styles/reset.css';

registerLocaleData(zh);

@NgModule({
  // ,
  declarations: [AppComponent],
  // module CommonModule,Nfif,Ngfor
  imports: [
    // Common
    CommonCoModule,
    // 
    ReactiveFormsModule,
    LayoutModule,
    PagesModule,
    BrowserModule,
    AppRoutingModule,
    IconsProviderModule,
    RouterModule,
    // ng-zorro-antd
    NgZorroAntdModule,
    FormsModule,
    CommonModule,
    HttpClientModule,
    BrowserAnimationsModule
  ],
  // serviceng-zorro-antd()
  providers: [
    { provide: NZ_I18N, useValue: zh_CN },
    {
      provide: APP_INITIALIZER,
      useFactory: StartupServiceFactory,
      deps: [StartupService],
      multi: true
    }
  ],
  // app, component. bootstrapentryComponents
  bootstrap: [AppComponent]
  // entryCompoenents: ngbootstrapngbootstrap componentdom
})
export class AppModule {}
