import { NgModule } from '@angular/core';
import { LayoutDefaultComponent } from '../layout/default/default.component';
import { Routes, RouterModule } from '@angular/router';

/**:
 * 1. pages-routing.module.ts ,   '#' --- dashboard/dashboard.module.ts --> TableModule
 * 2. dashboard/dashboard.module.ts   ---> ---- /dashboard-routing.module.ts
 * 3.  ----Table table.component.ts  
 * 4. Table HTML/css 
 */

const routes: Routes = [
  {
    path: 'workspace',
    component: LayoutDefaultComponent,
    children: [
      {
        path: '',
        redirectTo: 'houseManage',
        pathMatch: 'full'
      },
      {
        path: 'houseManage',
        data: {
          title: '',
         
        },
        loadChildren: () =>
          import('./houseManage/houseManage.module').then(
            m => m.HouseManageModule
          )
      },
      {
        path: 'operateManage',
        data: {
          title: '',
          reuse: true
        },
        loadChildren: './operateManage/operateManage.module#OperateManageModule'
      },
      {
        path: 'regionManage',
        data: {
          title: '',
          reuse: true
        },
        loadChildren: './regionManage/regionManage.module#RegionManageModule'
      },
      {
        path: 'adminstrators',
        data: {
          title: '',
          reuse: true
        },
        loadChildren: './adminstrators/adminstrators.module#AdminstratorsModule'
      },
      {
        path: 'projectManage',
        data: {
          title: '',
          reuse: true
        },
        loadChildren: './projectManage/projectManage.module#ProjectManageModule'
      },
      {
        path: 'brokerManage',
        data: {
          title: '',
          reuse: true
        },
        loadChildren: './brokerManage/brokerManage.module#BrokerManageModule'
      },
      {
        path: 'Operate',
        data: {
          title: '',
          reuse: true
        },
        loadChildren: './Operate/Operate.module#OperateModule'
      },
      {
        path: 'Activity',
        loadChildren: './Activity/Activity.module#ActivityModule'
      },
      {
        path: 'myuser',
        loadChildren: () =>
          import('./Myuser/Myuser.module').then(m => m.MyuserModule)
      }
    ]
  },
  {
    path: 'user',
    loadChildren: './user/user.module#UserModule'
  },

  {
    path: 'test',
    loadChildren: './test/test.module#TestModule'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class PagesRoutingModule {}
