import { NgModule } from '@angular/core';
// interface 
import { httpInterceptorProviders } from './httpClient/interceptor-config';
// 
import { CoolServerStorage } from './coolStorage/cool-server-storage';
// 
import { CoolLocalStorage } from './coolStorage/cool-local-storage';
// 
import { CoolSessionStorage } from './coolStorage/cool-session-storage';
// http
import { HttpClientService, ActivityHttpClientServie } from './httpClient/http';

// 
import { ZwRouteReuseStrategy } from './RouteReuseStrategy/RouteReuseStrategy';
import { RouteReuseStrategy } from '@angular/router';

@NgModule({
  imports: [],
  declarations: [],
  exports: [],
  providers: [
    { useClass: ZwRouteReuseStrategy, provide: RouteReuseStrategy },
    HttpClientService,
    ActivityHttpClientServie,
    CoolLocalStorage,
    CoolSessionStorage,
    CoolServerStorage,
    httpInterceptorProviders
  ]
})
export class CommonCoModule {}
